import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Link,
  useRouteMatch
} from "react-router-dom";
import "./Portfolio.css";
import CodePen from "react-codepen-embed";
import Car from "./Car.js";
import { digArt, compositions } from "./portfolioConstants";

const Portfolio = () => {
  const { path, url } = useRouteMatch();
  return (
    <div class="Portfolio">
      <Switch>
        <Route exact path={`${url}/`}>
          <h1>Portfolio</h1>
          <p>
            <Link to={`${url}/digart`}>Digital Art</Link>
          </p>
          <p>
            <Link to={`${url}/compositions`}>Music Compositions</Link>
          </p>
          <Car />
        </Route>
        <Route path={`${path}/digart`}>
          <h1>Digital Art</h1>
          {digArt.map(item => (
            <PortfolioItem piece={item.piece} desc={item.description} />
          ))}
        </Route>
        <Route path={`${path}/compositions`}>
          <h1>Compositions</h1>
          {compositions.map(item => (
            <PortfolioItem piece={item.piece} desc={item.description} />
          ))}
        </Route>
      </Switch>
    </div>
  );
};

const PortfolioItem = ({ piece, desc }) => {
  return (
    <div class="PortfolioItem">
      {piece}
      {desc}
    </div>
  );
};
export default Portfolio;
